div {
  text-align: center;
  margin: 15px auto;
}
form {
  text-align: center;
  margin: 15px auto;
  width: 600px;
  border-style: ridge;
}

.task {
  width: 500px;
  margin-top: 15px;
}

.submit {
  margin: 10px;
}

.todo-title {
  color: red;
  text-transform: uppercase;
  margin: 5px;
}

.complete {
  color: green;
  text-transform: uppercase;
  margin-top: 40px;
}

.list-item {
  width: fit-content;
  border-style: ridge;
}

.todo {
  width: 700px;
  display: flex;
  justify-content: center;
  border-style: solid;
  border-top-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 5px;
  margin: 5px;
  border-color: lightslategray;
}

button {
border-radius: 30px;
margin: 5px;
width: 80px;
}
